          <!-- <tr>
            <td>是否开通qq钱包</td>
            <td>{{tableData.qpayFlag | qpayFlagFilter}}</td>
          </tr>
          <tr>
            <td>qq钱包扣率</td>
            <td>{{tableData.qpaySetCd}}</td>
          </tr>
          <tr>
            <td>是否开通京东钱包</td>
            <td>{{tableData.jdpayFlag | jdpayFlagFilter}}</td>
          </tr>
          <tr>
            <td>京东扣率代码</td>
            <td>{{tableData.jdpaySetCd}}</td>
          </tr> -->
