          </tr>
          <tr>
            <td>其他资料1</td>
            <td>
              <img :src="imgFil(photoData.other1)" alt="" class="previewImg">
            </td>
          </tr>
          <tr>
            <td>组织机构代码证图片</td>
            <td>
              <img :src="imgFil(photoData.organizationCodeId)" alt="" class="previewImg">
            </td>
          </tr>
          <tr>
            <td>税务登记证图片</td>
            <td>
              <img :src="imgFil(photoData.registrationCodeId)" alt="" class="previewImg">
            </td>
          </tr>
          <tr>
            <td>事业单位证明函图片</td>
            <td>
              <img :src="imgFil(photoData.certificateInstitution)" alt="" class="previewImg">
            </td>
          </tr>
          <tr>
            <td>经营许可证图片</td>
            <td>
              <img :src="imgFil(photoData.businessLicense)" alt="" class="previewImg">
            </td>
          </tr>


        </table>


      </div>
    </el-card>


  </div>
</template>

<script>
  import {
    getFyIncomingInfo
  } from '../api/fy'
  import {url} from '@/utils/request'

  export default {
    name: "hsf",
    props: ['channlename'],
    data() {
      return {
        loading: false, //加载
        id: '',
        pc: '',
        na: '',
        tableData: {},
				photoData: {}
      }
    },
    mounted() {
      this.id = this.$route.query.id;
      this.pc = this.$route.query.pc;
      this.na = this.$route.query.na || '';

      this.getInfofun()

    },
		filters:{
			licenseTypeFilter(val){
				switch(val){
					case '0':
						return '营业执照';
					case '1':
						return '三证合一';
					case 'A':
						return '身份证';
					case 'B':
						return '个体户';
					case '2':
						return '事业单位';
					default:
						return '';
				}
			},
			dtFilter(val) {
				if(val == null || val == ''){
					return ''
				}
				return val[0] + val[1] + val[2] + val[3] + '-' + val[4] + val[5] + '-' + val[6] + val[7];
			},
			acntTypeFilter(val){
				switch(val){
					case '1':
						return '对公'
					case '2':
						return '对私'
					default:
						return '';
				}
			},
			acntArtifFlagFilter(val) {
				switch(val){
					case '0':
						return '非法人入账'
					case '1':
						return '法人入账'
					default:
						return '';
				}
			},
			acntCertifTpFilter(val) {
				switch(val){
					case '0':
						return '身份证'
					default:
						return '';
				}
			},
			wxFlagFilter(val) {
				if(val == '1'){
					return val;
				}
				return '未开通';
			},
			aliFlagFilter(val) {
				if(val == '1'){
					return val;
				}
				return '未开通';
			},
			qpayFlagFilter(val) {
				if(val == '1'){
					return val;
				}
				return '未开通';
			},
			jdpayFlagFilter(val) {
				if(val == '1'){
					return val;
				}
				return '未开通';
			},
			settleTpFilter(val) {
				switch(val){
					case '1':
						return '自动结算(T1)';
					case '2':
						return '手动结算';
					case '3':
						return '自动结算(D1)';
					case '4':
						return '定时结算';
					default:
						return '';
				}
			},
			hasReserveAcntFilter(val) {
				if(val == '1'){
					return '启动';
				}
				return '未启动';
			},
		},
    methods: {
      /**
       * 获取信息
       */
      getInfofun: function () {
        getFyIncomingInfo(this.id).then(res => {
          console.log(res);
          this.tableData = res.obj.fuiouMchInfo;
					this.photoData = res.obj.fuiouMchPhoto;
        }).catch(e => {

        })
      },

      /**
       * 跳出
       */
      jumpOut: function () {
        this.$emit('gobackbutton')
      },

      /**
       *获取图片  进件专用的方法
       */
      imgFil: function (val) {

        if (val) {
          if (val === '' || val == null) {
            return ''
          }
          let arrayStr = val.split('\\'); // 凭\\分割出文件名
          if(arrayStr.length>4){
            return url + '/filepath/'+arrayStr[(arrayStr.length - 2)]+"/" + arrayStr[(arrayStr.length - 1)];
          }else{
            return url + '/filepath/' + arrayStr[(arrayStr.length - 1)];
          }
        } else {
          let base64 = 'data:image/png;base64,';
          return base64
        }

      },

    }
  }
</script>

<style scoped>

  .info-table {
    width: 100%;
  }

  .info-table tr {
    font-size: 14px;
    color: #3A3A3A;
    line-height: 40px;
  }

  .info-table tr td:nth-child(1) {
    padding-right: 20px;
    text-align: right;
    width: 150px;
  }

  .info-table tr td:nth-child(2) {

  }

  .info-table tr td:nth-child(2) img {
    width: 200px;
    height: auto;
  }


</style>



